.modal {
  position: fixed;
  right: 0;
  top: 0;
  z-index: 101;
  display: none;
  overflow-y: auto;
  width: 100%;
  height: 100%;
  background-color: var(--color-white);
  opacity: 1;
  transform: translateX(0);
}

.modal--visible {
  display: block;
}

.modal__close-btn {
  content: "";
  position: absolute;
  right: 16px;
  top: 24px;
  z-index: 5;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  width: 25px;
  height: 25px;
  font-size: 0;
  background-color: var(--color-white);
  cursor: pointer;
}

.modal__close-btn::before,
.modal__close-btn::after {
  content: "";
  position: absolute;
  border: 1px solid #000;
  border-radius: 2px;
  width: 15px;
  height: 2px;
  background-color: #000;
}

.modal__close-btn::before {
  transform: rotate(45deg);
}

.modal__close-btn::after {
  transform: rotate(-45deg);
}

.modal__title {
  font-size: 20px;
  line-height: 25px;
  font-family: 'Gilroy', 'Arial', sans-serif;
  font-weight: 600;
  margin: 24px 0;
  padding: 0 60px 0 16px;
  color: var(--color-black);
}

.modal__body {
  border-top: 1px solid #dfdfdf;
  padding-top: 24px;
}

.modal__column {
  margin: 0 16px;
}

.modal__item {
  margin-bottom: 40px;
}

.modal__item-title {
  font-size: 16px;
  line-height: 20px;
  font-family: 'Gilroy', 'Arial', sans-serif;
  font-weight: 500;
  margin: 0 0 16px;
  padding: 0;
  color: var(--color-black);
}

.modal__range,
.modal__list,
.modal__radio {
  margin: 0;
  border: none;
  padding: 0;
}

.modal__list {
  margin-left: 18px;
}

.modal__checkbox-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.modal__checkbox-input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.modal__checkbox-input + .modal__checkbox-label {
  display: inline-flex;
  align-items: center;
}

.modal__checkbox-input + .modal__checkbox-label::before {
  content: "";
  display: inline-block;
  margin-right: 8px;
  border: 1px solid #b5b5b5;
  border-radius: 5px;
  width: 18px;
  height: 18px;
  background-position: center center;
  background-size: 50% 50%;
  background-repeat: no-repeat;
}

.modal__checkbox-input:checked + .modal__checkbox-label::before {
  background-color: var(--color-black);
}

.modal__checkbox-label {
  font-size: 16px;
  line-height: 20px;
  font-family: 'Gilroy', 'Arial', sans-serif;
  font-weight: 500;
  color: var(--color-dark-grey);
}

.modal__radio {
  display: flex;
}

.modal__radio-option {
  margin-right: 80px;
}

.modal__radio-option:last-child {
  margin-right: 0;
}

.modal__radio-input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.modal__radio-input + .modal__radio-label {
  display: inline-flex;
  align-items: center;
}

.modal__radio-input + .modal__radio-label::before {
  content: "";
  display: inline-block;
  margin-right: 8px;
  border: 1px solid #b5b5b5;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  background-position: center center;
  background-size: 50% 50%;
  background-repeat: no-repeat;
}

.modal__radio-input:checked + .modal__radio-label::before {
  background-color: var(--color-black);
}

