}

.modal__btn {
  font-size: 16px;
  line-height: 20px;
  font-family: 'Gilroy', 'Arial', sans-serif;
  font-weight: 600;
  margin: 0;
  border: none;
  padding: 15px 0;
  text-align: center;
  color: var(--color-dark-grey);
  background-color: #dfdfdf;
  cursor: pointer;
}

